import React from 'react';
import { Button } from 'react-bootstrap';
import {Link} from "react-router-dom";
import Logo from "../../images/logo.png";

// styles
import "./header.styles.scss";

const Header = () => (
    <header>
        <div className="row no-gutters header-row">
            <div className="header-top col-lg-12">For all the car lovers out there!</div>
        </div>
        <div className="row mt-4">
            <div className="col-lg-4"><Button variant="dark">Accessories store</Button> <Button variant="outline-secondary">Premium services</Button></div>
            <div className="col-lg-4 text-center"><Link to="/"> <img src={Logo} alt="service genie" className="logo" /></Link></div>
            <div className="col-lg-4 text-right"><Button variant="link" className="link-btn">Sign up</Button> | <Button variant="link" className="link-btn">Login</Button></div>
        </div>
    </header>
);

export default Header;